function countStratRF(vCounterRF, vScaleTitle) {
    let vStratRF = '';
    switch (vScaleTitle) {
        case 'Padua':
            vCounterRF > 3 ? vStratRF = '–≤—ã—Å–æ–∫–∏–π' : vStratRF = '–Ω–∏–∑–∫–∏–π';
            return vStratRF;
        case 'IMPROVE':
            vCounterRF > 7 ? vStratRF = '–≤—ã—Å–æ–∫–∏–π' : vStratRF = '–Ω–∏–∑–∫–∏–π';
            return vStratRF;
        case 'HAS_BLED':
            vCounterRF > 2 ? vStratRF = '–≤—ã—Å–æ–∫–∏–π' : vStratRF = '–Ω–∏–∑–∫–∏–π';
            return vStratRF;
        case 'CHA2DS2_VAS—ÅOrRusSurgOrTraumRF':
            vCounterRF == 0 ? vStratRF = '–Ω–∏–∑–∫–∏–π' : vCounterRF >= 1 && vCounterRF <= 2 ? vStratRF = '—É–º–µ—Ä–µ–Ω–Ω—ã–π' : vStratRF = '–≤—ã—Å–æ–∫–∏–π';
            return vStratRF;
        case 'Caprini':
            vCounterRF == 0 ? vStratRF = '–Ω–∏–∑–∫–∏–π' : vCounterRF >= 1 && vCounterRF <= 2 ? vStratRF = '—É–º–µ—Ä–µ–Ω–Ω—ã–π' : vCounterRF >= 3 && vCounterRF <= 4 ? vStratRF = '–≤—ã—Å–æ–∫–∏–π' : vStratRF = '–æ—á–µ–Ω—å –≤—ã—Å–æ–∫–∏–π';
            return vStratRF;
        case 'SurgOrTraumBleedingRF':
            vCounterRF >= 1 ? vStratRF = '–≤—ã—Å–æ–∫–∏–π' : vStratRF = '–Ω–∏–∑–∫–∏–π';
            return vStratRF;
        case 'GreenTop37aRus':
            vCounterRF === 1 ? vStratRF = '—É–º–µ—Ä–µ–Ω–Ω—ã–π' : vCounterRF > 1 ? vStratRF = '–≤—ã—Å–æ–∫–∏–π' : vStratRF = '–Ω–∏–∑–∫–∏–π';
            return vStratRF;
    }
}

